@import '../../assets/styles/partial';
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);

.calendar {
  width: 573px;
  height: 481px;
  border-radius: 5px;
  background: #EBE7FF;

  &__header {
    height: 57px;
    background: $purpleColor;
    border-radius: 5px 5px 0 0;
    display: flex;
    align-items: center;
    justify-content: center; }

  &__month {
    @extend %nav-font;
    line-height: 16.6px;
    color: white;
    padding: 0 38px; }

  &__btn {
    padding: 2px;
    cursor: pointer; }

  &__daysOfWeek {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 37px;
    font-weight: 300;
    font-size: 14px;
    line-height: 16.6px;
    padding: 0 10px; }

  &__dayOfWeek {
 }    //padding:

  &__cells {
    padding: 0 10px; }

  &__cell {
    padding: 10px;
    width: 79px;
    height: 63px;
    background: white;
    font-weight: 400;
    font-size: 20px;
    line-height: 20px;
    display: flex;
    &_isDisabled {
      color: rgba(0,0,0,0.25);
      background: inherit;
      pointer-events: none; }
    &_isSelected {
      border: 1px red solid; } }
  &__row {
    display: flex; }

  &__meetings {
    @extend %nav-font;
    color: white;
    line-height: 22px;
    text-align: center;
    width: 22px;
    height: 22px;
    margin-left: auto;
    margin-top: auto;
    border-radius: 50px;
    background: $blueColor; } }





